.game {
	height: auto;
	overflow: hidden;
	margin: 0 auto;
}

body,
html {
	background-color: rgb(40, 40, 40);
}

.control-buttons {
	height: auto;
	display: flex;
	flex-flow: column wrap;
	width: 100%;
	justify-content: center;
	align-items: center;
	background-color: rgb(40, 40, 40);
}

button {
	width: 100px;
	height: 30px;
	margin: 10px auto;
	display: block;
}

label {
	color: #fff;
}

input {
	width: 100px;
}
